.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.header__subtitle {
  font-size: 14px;
}

.training__subtitle {
  font-size: 16px;
}

.content__item__subtitle {
  display: block;
  padding-bottom: 40px;
  font-size: 14px;
  font-weight: 700;
}

.content__item__row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  padding-top: 20px;
}

.content__item__col-4 {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
}

.content__item__col-12 {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(100% - 10px);
}

.pt-20 {
  padding-top: 20px;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  text-align: center;
}

body {
  color: #989898;
  background-color: #ffffff;
}

.header {
  margin-bottom: 20px;
}

.header h1 {
  font-size: 25px;
  font-weight: 600;
  text-transform: uppercase;
}

.training h2 {
  color: #0A27A2;
  font-size: 22px;
  font-weight: 600;
}

.uppercase {
  text-transform: uppercase;
}

.training__subscribe-button {
  border: none;
  background-color: #0A27A2;
  padding: 14px 28px;
  font-size: 16px;
  cursor: pointer;
  color: #ffffff;
  display: inline-block;
  width: 400px;
}

.training__subscribe-button span {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 18px;
}

.training__video {
  padding-top: 20px;
  position: relative;
  margin: -5px;
}

.training__video::before {
  content: "";
  position: absolute;
  top: 35%;
  left: 48%;
  width: 0;
  height: 0;
  border-top: 30px solid transparent;
  border-left: 50px solid #0A27A2;
  border-bottom: 30px solid transparent;
  cursor: pointer;
}

.training__subscribe-form {
  background-color: #989898;
  width: 400px;
  margin: 0 auto;
}

.training__subscribe-form input[type="text"] {
  box-sizing: border-box;
  width: 380px;
  padding: 10px;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 15px;
  font-size: 20px;
  font-style: inherit;
  font-weight: 400;
  color: #989898;
}

.content {
  margin-top: 20px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 500;
}

.content__item h3 {
  font-size: 20px;
  line-height: 1.5;
  color: #0A27A2;
  font-weight: 600;
}

.content__item p {
  font-size: 18px;
  font-weight: 600;
}

.content__item {
  width: 700px;
  margin: 0 auto;
}

.footer {
  background-color: #989898;
  text-align: center;
  color: #ffffff;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 30px;
}

.footer__menu {
  text-transform: uppercase;
}

.footer__menu li a {
  color: inherit;
  text-decoration: none;
  display: block;
  padding-top: 5px;
  padding-bottom: 5px;
}

.footer__menu li a:hover {
  color: #0A27A2;
}

.social {
  width: 400px;
  margin-top: 30px;
  margin: 20px auto;
}

.social ul {
  display: flex;
  justify-content: space-between;
}

.social ul li a {
  color: #ffffff;
}

.social ul a:hover {
  color: #0A27A2;
}

.social-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #989898;
  text-decoration: none;
  font-size: 22px;
}

.personal-checkbox {
  position: relative;
  padding-left: 30px;
  color: #ffffff;
}

.personal {
  display: flex;
  align-items: center;
  padding-left: 11px;
  font-size: 12px;
  padding-top: 12px;
  padding-bottom: 10px;
}

.personal-checkbox {
  align-self: center;
}

.personal-checkbox input[type="checkbox"] + .checkbox-indicator {
  position: absolute;
  top: -1px;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: #ffffff;
}

.personal-checkbox input[type="checkbox"]:focus + .checkbox-indicator {
  border-color: #989898;
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::before,
.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 1px;
  width: 17px;
  height: 2px;
  background-color: #989898;
}

.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::before {
  transform: rotate(45deg);
}

.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::after {
  transform: rotate(-45deg);
}

.footer-copyright {
  font-size: 12px;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 580px) {
  .content__item {
    width: auto;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}