.reset();
.wrapper {
	.wrapper();
	margin: 0 auto;
	text-align: center;
}
body {
	color: @grey;
	background-color: @white;
}
.header{
	margin-bottom: 20px;
	h1{
		font-size: 25px;
		font-weight: 600;
		text-transform: uppercase;
	}
}
.training{
	h2{
		color: @dark-blue;
		font-size: 22px;
		font-weight: 600;
	}
}
.uppercase{
	text-transform: uppercase;
}
.training__subscribe-button {
	border: none;
	background-color: @dark-blue;
	padding: 14px 28px;
	font-size: 16px;
	cursor: pointer;
	color: @white;
	display: inline-block;
	width: 400px;

	span {
		text-transform: uppercase;
		font-weight: 600;
		font-size: 18px;
	}
}
.training__video{
	padding-top: 20px;
	position: relative;
	margin: -5px;
}

.training__video {
	&::before{
		content:"";
		position: absolute;
		top: 35%;
		left: 48%;
		width: 0; 
		height: 0;
		border-top: 30px solid transparent;
		border-left: 50px solid @dark-blue;
		border-bottom: 30px solid transparent;
		cursor: pointer;
	}
	
}
.training__subscribe-form {
	background-color: @grey;
	width: 400px;
	margin: 0 auto;
	& input[type="text"] {
		box-sizing: border-box;
		width: 380px;
		padding: 10px;
		margin-left: 10px;
		margin-right: 10px;
		margin-top: 15px;
		font-size: 20px;
		font-style: inherit;
		font-weight: 400;
		color: @grey;
	}
}

.content {
	margin-top: 20px;
	font-size: 18px;
	line-height: 1.5;
	font-weight: 500;
}
.content__item {
	
	h3 {
		font-size: 20px;
		line-height: 1.5;
		color: @dark-blue;
		font-weight: 600;
		
	}
	p{
		font-size: 18px;
		font-weight: 600;
	}
}
.content__item{
	width: 700px;
	margin: 0 auto;
}
.footer {
	background-color: @grey;
	text-align: center;
	color: @white;
	padding-top: 20px;
	padding-bottom: 20px;
	margin-top: 30px
}
.footer__menu {
	text-transform: uppercase;
	li a {
		color: inherit;
		text-decoration: none;
		display: block;
		padding-top: 5px;
		padding-bottom: 5px;
		&:hover{
			color: @dark-blue;
		}
	
}
}
.social{
	width: 400px;
	margin-top: 30px;
	
	margin: 20px auto;
	ul {
		display: flex;
		justify-content: space-between;	
	li a {
		color: @white;
	}
	a:hover{
		color: @dark-blue;
	}
}
}
.social-button {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 40px;
	background-color: @grey;
	text-decoration: none;
	font-size: 22px;

}
.personal-checkbox{
	position: relative;
	padding-left: 30px;
	color:@white;
	
}
.personal	{
	display: flex;
	align-items: center;
	padding-left: 11px;
	font-size: 12px;
	padding-top: 12px;
	padding-bottom: 10px;
}
.personal-checkbox{
	align-self: center;
}
.personal-checkbox input[type="checkbox"] + .checkbox-indicator{
  position: absolute;
  top: -1px;
  left: 0;
  width: 18px;
  height: 18px;
	background-color: @white;
}
.personal-checkbox input[type="checkbox"]:focus + .checkbox-indicator{
  border-color: @grey;
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
}
.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::before,
.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 1px;
  width: 17px;
  height: 2px;
  background-color: @grey;
}
.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::before{
  transform: rotate(45deg);
}
.personal-checkbox input[type="checkbox"]:checked + .checkbox-indicator::after{
  transform: rotate(-45deg);
}
.footer-copyright{
	font-size: 12px;
}
